<template><section><h1>后期处理</h1>
<p><code v-pre>vc-stage-process-post</code> 组件用于加载一组后期处理，如加载自定义着色器。</p>
<h2>示例</h2>
<h3>加载后期处理的着色器</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-stage-process-post :fragmentShader="fragmentShader"></vc-stage-process-post>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-stage-process-post</span> <span class="hljs-attr">:fragmentShader</span>=<span class="hljs-string">"fragmentShader"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-stage-process-post</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="actionscript">
  export <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        fragmentShader: `
          uniform sampler2D colorTexture;
          varying vec2 v_textureCoordinates;
          float hash(float x){
            <span class="hljs-keyword">return</span> fract(sin(x*<span class="hljs-number">23.3</span>)*<span class="hljs-number">13.13</span>);
          }
          <span class="hljs-keyword">void</span> main(<span class="hljs-keyword">void</span>){
            float time = czm_frameNumber / <span class="hljs-number">60.0</span>;
            vec2 resolution = czm_viewport.zw;
            vec2 uv=(gl_FragCoord.xy*<span class="hljs-number">2.</span>-resolution.xy)/min(resolution.x,resolution.y);
            vec3 c=vec3(<span class="hljs-number">.6</span>,<span class="hljs-number">.7</span>,<span class="hljs-number">.8</span>);
            float a=<span class="hljs-number">-.4</span>;
            float si=sin(a),co=cos(a);
            uv*=mat2(co,-si,si,co);
            uv*=length(uv+vec2(<span class="hljs-number">0</span>,<span class="hljs-number">4.9</span>))*<span class="hljs-number">.3</span>+<span class="hljs-number">1.</span>;
            float v=<span class="hljs-number">1.</span>-sin(hash(floor(uv.x*<span class="hljs-number">100.</span>))*<span class="hljs-number">2.</span>);
            float b=clamp(abs(sin(<span class="hljs-number">20.</span>*time*v+uv.y*(<span class="hljs-number">5.</span>/(<span class="hljs-number">2.</span>+v))))<span class="hljs-number">-.95</span>,<span class="hljs-number">0.</span>,<span class="hljs-number">1.</span>)*<span class="hljs-number">20.</span>;
            c*=v*b;
            gl_FragColor = mix(texture2D(colorTexture, v_textureCoordinates), vec4(c,<span class="hljs-number">1</span>), <span class="hljs-number">0.5</span>);
          }  `
      }
    },
    methods: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">this</span>.cesiumInstance = cesiumInstance
        <span class="hljs-keyword">const</span> { Cesium, viewer } = <span class="hljs-keyword">this</span>.cesiumInstance
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>fragmentShader</td>
<td>String</td>
<td></td>
<td><code v-pre>required</code> 指定着色器代码。</td>
</tr>
<tr>
<td>uniforms</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定着色器 uniforms 参数。uniform变量一般用来表示：变换矩阵，材质，光照参数和颜色等信息。</td>
</tr>
<tr>
<td>textureScale</td>
<td>Number</td>
<td><code v-pre>1.0</code></td>
<td><code v-pre>optional</code> 指定纹理尺寸缩放比例，取值范围 (0.0, 1.0] 。</td>
</tr>
<tr>
<td>forcePowerOfTwo</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 是否强制将纹理尺寸都等于2的幂。 2的幂将是最小维度中2的下一个幂。</td>
</tr>
<tr>
<td>sampleMode</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 指定输入颜色纹理的采样方式。 <strong>{NEAREST: 0, LINEAR: 1}</strong></td>
</tr>
<tr>
<td>pixelFormat</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定输出纹理的像素格式。</td>
</tr>
<tr>
<td>pixelDatatype</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定输出纹理的数据类型。</td>
</tr>
<tr>
<td>clearColor</td>
<td>Object|Array|String</td>
<td><code v-pre>BLACK</code></td>
<td><code v-pre>optional</code> 指定清除输出纹理的颜色。</td>
</tr>
<tr>
<td>scissorRectangle</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定用于测试的矩形。</td>
</tr>
<tr>
<td>name</td>
<td>String</td>
<td></td>
